html, body {
  overflow: hidden;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: 'Inter', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


.app-container {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  background: linear-gradient(to bottom, #219ebc, #8ecae6);
  padding: 24px;
  color: white;
  width: 100%;
  overflow-y: auto;
}

.title {
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 8px;
  width: 100%;
  max-width: 1100px;
}

.title-line {
  width: 100%;
  max-width: 1100px;
  border: 1px solid white;
  margin-bottom: 20px;
}

.grid-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 24px;
  width: 100%;
  max-width: 1100px;
  box-sizing: border-box;
}

.box {
  flex: 0 1 500px;
  background-color: #023047;
  padding: 24px 24px;
  border-radius: 12px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  text-align: left;
  border: 1px solid #3b82f6;
  box-sizing: border-box;
}
.box:hover {
  transform: translateY(-2px);
  transition: all 0.2s ease;
  box-shadow: 0 8px 20px rgba(0,0,0,0.2);
}


.file-input {
  margin-bottom: 16px;
  padding: 8px;
  border-radius: 8px;
  background-color: white;
  color: black;
}

.upload-button {
  display: flex;
  align-items: center;
  gap: 10px;
  background-color: #219ebc;
  color: white;
  padding: 12px 20px;
  border-radius: 12px;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  border: none;
  transition: background 0.3s ease;
  width: fit-content;
  margin-top: 12px;
}

.upload-button:hover {
  background-color: #8ecae6;
  color: #023047;
}

.result-text {
  font-size: 1rem;
  font-weight: 500;
  color: #cfe9f5;
  margin-bottom: 8px;
  text-align: left;
}

.bold {
  font-weight: 600;
}

.error-text {
  color: #f87171;
}

.placeholder-text {
  color: #d1d5db;
  font-size: 1.1rem;
}

.preview-container {
  margin-top: 16px;
}

.preview-image {
  width: 250px;
  height: 250px;
  object-fit: cover;
  border-radius: 8px;
  border: 3px solid #219ebc;
}

.custom-file-upload {
  display: flex;
  align-items: center;
  gap: 10px;
  background-color: #219ebc;
  color: white;
  padding: 12px 20px;
  border-radius: 12px;
  cursor: pointer;
  font-weight: 500;
  font-size: 1rem;
  width: fit-content;
  transition: background 0.3s ease;
  margin-bottom: 16px;
}

.custom-file-upload:hover {
  background-color: #8ecae6;
  color: #023047;
}

.custom-file-upload input[type="file"] {
  display: none;
}

.upload-icon {
  font-size: 1.4rem;
}

h2 {
  font-size: 1.6rem;
  font-weight: 600;
  margin-bottom: 30px;
  color: #ffffff;
}
h3 {
  font-size: 1.1rem;
  font-weight: 500;
  color: #cfe9f5;
}
.logo-container {
  display: flex;
  align-items: center;
  gap: 16px;
  margin-bottom: 8px;
}

.logo-image {
  width: 50px;
  height: 50px;
  object-fit: contain;
  padding-top: 20px;
}

.diagnosis-box {
  margin-top: 8px;
  padding: 10px 16px;
  border: 1px solid #fb8500;
  background-color: #023047;
  color: #fb8500;
  font-weight: 600;
  font-size: 1.1rem;
  border-radius: 8px;
  display: inline-block;
  align-items: center;
}


.chart-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 16px;
}

.confidence-label {
  font-size: 1rem;
  color: #cfe9f5;
  margin-top: 8px;
}
